import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";

/**Actions Redux */
import {
  mostrarAlertaAction,
  ocultarAlertaAction,
} from "../../actions/alertaActions";
import { mostrarEstadoLoginAction } from "../../actions/estadoLoginActions";
import styled from "styled-components";

const Contenedor = styled.div`    
height:100vh;
display: grid;
background-color: #0F0C5A;
grid-template-columns: repeat(4, 1fr);
grid-gap: 10px;
grid-auto-rows: 350px;
grid-template-areas: 
  ". a a ."
  ". a a .";  
}`;

const SideBar = styled.div`
  grid-area: a;
  align-self: center;
  justify-self: center;
  width: 50%;
`;

export const Login = ({ history }) => {
  /**State del componente */
  const [nombre, guardarnombre] = useState("");
  const [cedula, guardarCedula] = useState("");

  /**Acceder al state del store */
  const cargando = useSelector((state) => state.usuarios.state);
  const error = useSelector((state) => state.usuarios.error);
  const alerta = useSelector((state) => state.alerta.alerta);

  /**Utilizar dispatch y crear funcion */
  const dispatch = useDispatch();

  /**Enviando Datos */
  const submitNuevoUsuario = (e) => {
    e.preventDefault();

    /**Validar Formulario */
    if (nombre.trim() === "" || cedula.trim() === "") {
      const alerta = {
        msg: "Campos Obligatorios",
        classes: "alert alert-danger text-center text-uppercase p3",
      };

      dispatch(mostrarAlertaAction(alerta));
      dispatch(mostrarEstadoLoginAction(true));
      return;
    }

    /**Si no hay errores */
    dispatch(ocultarAlertaAction());
    dispatch(mostrarEstadoLoginAction(false));

    history.push("/");
  };

  return (
    <Contenedor>
      <SideBar>
        <div class="card">
          <div class="card-body">
            <div className="col-md-12">
              <div className="align-self-center text-center mb-2 ml-3">
                <a className="navbar-brand">
                  <img
                    src="https://w7.pngwing.com/pngs/413/324/png-transparent-barrancabermeja-logo-ecopetrol-business-product-eco-energy-text-logo-business.png"
                    className="rounded"
                    width="125"
                    height="35"
                    alt=""
                  />
                </a>
              </div>
              <h2 className="text-center mb-4 font-weight-bold">Login</h2>

              {alerta ? <p className={alerta.classes}>{alerta.msg}</p> : null}

              <form onSubmit={submitNuevoUsuario}>
                <div className="form-group">
                  <label>Usuario y/o Correo</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Usuario"
                    name="nombre"
                    value={nombre}
                    onChange={(e) => guardarnombre(e.target.value)}
                  />
                </div>

                <div className="form-group">
                  <label>Cedula</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Cedula"
                    name="cedula"
                    value={cedula}
                    onChange={(e) => guardarCedula(e.target.value)}
                  />
                </div>

                <button
                  type="submit"
                  className="text-uppercase d-block w-100 mt-4 mb-3"
                >
                  Ingresar
                </button>
              </form>

              {cargando ? <p>Cargando..</p> : null}
              {error ? (
                <p className="alert alert-danger p2 mt-4 text-center">
                  Hubo un error
                </p>
              ) : null}
            </div>
          </div>
        </div>
      </SideBar>
    </Contenedor>
  );
};
